/* Styling for Project Card component */

.project__container {
  max-height: 300px;
  margin-bottom: 1em;
}

.project__container:focus {
  outline: none;
}

.project__details {
  position: relative;
  background-color: rgb(75, 109, 193);
  overflow: hidden;
  width: 100%;
  height: 50vw;
  max-height: 300px;
  border-radius: 20px;
  padding: 2em;
  color: white;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.project__details--grey {
  box-shadow: none;
}

.project__overlay {
  position: absolute;
  visibility: visible;
  top: 0;
  left: 0;
  background-color: white;
  overflow: hidden;
  width: 100%;
  height: 100%;
  border-radius: 20px;

  /* Make overlay slightly larger than what it's covering to avoid weird looking corners */
  transform: scale(1.1, 1.1);
  transform-origin: center;
}

/* This doesn't work on IE/Edge, so we're doing it with JS as well, but the JS version creates a flicker, so we're doing both */
.project__container:focus-within .project__overlay {
  visibility: hidden;
}

.project__overlay--hidden {
  visibility: hidden;
}

.project__img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.project__img--grey {
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
}

